$day-width: 100px;
$day-height: 100px;
$gap: 1px;
$border-size: 1px;
$amount-day-in-week: 7;
$calendar-width: $day-width * $amount-day-in-week + $gap *
  ($amount-day-in-week - 1) + 20;
$calendar-color: #eee;
$week-day: mon 1, tue 2, wed 3, thu 4, fri 5, sat 6, sun 7;
$active-color: rgb(249, 179, 191);
